@import '../pages/styles.scss';


/* Button used to open the chat form - fixed at the bottom of the page */
.openbutton {
    background-color:red;
    color: white;
    border: none;
    cursor: pointer;
    position: fixed;
    bottom: 300px;
    right: -85px;
    width: 200px;
    transform: rotate(-90deg);

  }

  .closebutton {
    display: none;

  }

  /* The popup chat - hidden by default */
  .chatclosed {
    display: none;
  }
  
  .chatopen {
    display: block;
    position: fixed;
    bottom: 0;
    right: 1px;
    z-index: 9;
    background-color: rgb(255, 255, 255);
    border: none;
    border-radius: 1em;

  }
  /* Add styles to the form container */
  .formcontainer{
    max-width: 300px;
    padding: 10px;
    background-color: rgb(255, 255, 255);
    z-index: 9;
    border-radius: 1em;


fieldset{
  border: none;

}
label{
  margin: 0;
  padding: 0;
}
.heading{
  background-color: rgb(0, 132, 255);
  margin: 0;
  padding: 0.6em;
  color: white;
  border-radius: 0.4em;
  height: 3em;
  font-size: large;
}
     /* Full-width textarea */
 textarea {
    width: 100%;
    padding: 15px;
    margin: 5px 0 22px 0;
    border-radius: 0.2em;
    border: none;
    background: #f1f1f1ed;
    resize: none;
    min-height: 10em;
  }

  input{
    width: 100%;
    padding: 15px;
    margin: 5px 0 22px 0;
    border-radius: 0.2em;
    border: none;
    resize: none;
    height: 1.5em;
    background: #f1f1f1ed;
  }

   /* When the textarea gets focus, do something */
   textarea:focus {
    background-color: #ddd;
    outline: none;
  }
  
  /* Set a style for the submit/send button */
  .submit_btn {
    background-color: $primary-color;
    color: white;
    padding: 16px 20px;
    border: none;
    cursor: pointer;
    width: 100%;
    margin-bottom:0.5em !important;
    margin-left: 0;
  }

  /* Add a red background color to the cancel button */
  .cancel {
    background-color: red;
    color: white;
    padding: 16px 20px;
    border: none;
    cursor: pointer;
    width: 100%;
    margin-bottom:10px;
    margin-left: 0;
  }

    
  /* Add some hover effects to buttons */
 .btn:hover, .openbutton:hover {
    opacity: 1;
  }

  }
  
 
  
$breakpoint-tablet: 800px;
@media only screen and (max-width: $breakpoint-tablet) {

  input{
    width: 100%;
    padding: 5px;
    margin: 2px 0 5px 0;
    border-radius: 0.2em;
    border: none;
    resize: none;
    height: 1.5em;
    background: #f1f1f1ed;
  }

  textarea {
    width: 100%;
    margin: 5px 0 22px 0;
    border-radius: 0.2em;
    border: none;
    background: #f1f1f1ed;
    resize: none;
    min-height: 6em !important;
  }

}
 

  

